import React from "react";

class Hobbies extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return(
            <div className="col-md-12">
                <p className="employee-info">Hobbies of {this.props.employeeInfo}</p>
            </div>
        );
    }
}

export default Hobbies;