import React from "react";
import history from "./History";

class Header extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        console.log(history);
        return(
            <div className="container-fluid">
                <div className="home-page-header">
                {history.location.pathname !== '/' && <label className="home-back-btn" onClick = {history.goBack} ><span className="home-back-symbol">&#8249;</span></label> }
                <div className="header-p"><p className="header-menu-word">
                    Home</p></div>
                </div>
                <br />
            </div>
        );
    }
}

export default Header;